import React from 'react';
import { useRef, useState, useEffect } from 'react';
import Webcam from 'react-webcam';
import { useNavigate, useLocation } from 'react-router-dom';
import PageWrapper from '../../../../layout/PageWrapper/PageWrapper';
import Page from '../../../../layout/Page/Page';
import { cirurgicalFlow } from '../../../../menu';
import Input from '../../../../components/bootstrap/forms/Input';
import Button, { ButtonGroup } from '../../../../components/bootstrap/Button';
import Modal, {ModalBody,ModalFooter,ModalHeader,ModalTitle} from '../../../../components/bootstrap/Modal';
import Header from '../../../../components/Header';
import  {DropdownItem, DropdownMenu, DropdownToggle} from '../../../../components/bootstrap/Dropdown';
import Dropdown from '../../../../components/bootstrap/Dropdown';
import Card, {CardBody} from '../../../../components/bootstrap/Card';
import Icon from '../../../../components/icon/Icon';
import API, {api} from '../../../../Services';


import NumericKeyboard from '../../../../components/Teclado';
// eslint-disable-next-line no-unused-vars
import style from './style.module.css'



const AssinaturaRecebimento = () => {
	

const navigate = useNavigate()
const location = useLocation()


const webRef 	= useRef(null);
const inputRef 	= useRef(null);	

// useStates 
const [DadosPaciente, 					setDadosPaciente				]	= useState([])
const [data, 							setData							] 	= useState('')
const [ModalUsuarioNaoEncontrado, 		setModalUsuarioNaoEncontrado	] 	= useState(false)
const [modalSuccess, 					setModalSuccess					] 	= useState(false)
const [Loading, 						setLoading						] 	= useState(false)


const [msgErrorAss,  	setMsgErrorAss] 			= useState('')
const [modalErrorMv, 	setModalErrorMv] 			= useState(false)
const [nofaceS3Error, 	setNofaceS3Error] 			= useState(false)




// Ao entrar na página, inicialmente, processará os dados abaixo.
useEffect(() => {
	
	console.warn('DEBUG')
	console.log('============= O QUE ESTÁ CHEGANDO POR PARÂMETRO  ==================================\n')
	console.log(location.state)
	console.log('============= FIM =================================================================\n')
	
	if(location.state) {
		setDadosPaciente(location.state)
	 }else{
		 navigate('/')
	 }
	
}, [location, data, navigate]);


const alterarLocalidade = () => {
	localStorage.removeItem('CAPP-local');
	navigate('/')
};


// Método de operações do Teclado
const childToParent = (childData) => {
	const valor = childData;
		if(valor === 'apagar'){
			const valorMenos = inputRef.current.value.substring(0, inputRef.current.value.length - 1)
			inputRef.current.value = valorMenos;
			console.log(`Valor do input: ${inputRef.current.value}`)
		}
		else if(valor === 'zerar'){
			inputRef.current.value = '';
			console.log(`Valor do input: ${inputRef.current.value}`)
		}else{
			setData(valor);
			inputRef.current.value += valor;
		}
}	


// Definição de Resolução de Vídeo, onde irá espelhar o rosto do usuário ao realizar o reconhecimento facial.
const videoConstraints = {
	width: 540,
	height: 880,
	facingMode: "user"
};


// Método responsável em registrar o roto para realizar o reconhecimento facial.
const capturarRosto = () => {
	return webRef.current.getScreenshot();
};


// Método responsável em inserir numa tabela de log a interação que é realizada pelo usuário.
const registraHistoricoAutenticacao = async ( codigoAviso, codigoEtapa, codigoCracha, autenticado, nomeArquivo ) => {
	let response;
	try {
		const request  = await API.post('/salvarHistoricoAutenticacao', { 'aviso': codigoAviso, 'badge': codigoCracha, 'filename': nomeArquivo, 'autenticado': autenticado, 'etapa': codigoEtapa});
		response = request.data;
	} catch (error) {
		console.log(error)
	}
	return response;
}


// Método responsável em registrar o rosto do assinante no servidor.
const uploadPhoto = async (imagem, codigoCracha, codUsuario, codigoAviso, codigoEtapa, nomeArquivo, autenticado) =>{
	let response;
	try {
		const request  = await API.post(`/uploadImage`, {'autenticado': autenticado, 'image': imagem, 'badge': codigoCracha, 'filename': nomeArquivo, 'user': codUsuario, 'etapa': codigoEtapa, 'aviso': codigoAviso})
		response = request.data;
	} catch (error) {
		console.log(error)
	}
	return response;
}


// Método responsável em abrir modal de Sucesso
const abreModalSucess = () => {
	setLoading		(false)
	setModalSuccess	(true)
}


// Método responsável em abrir modal de Error
const abreModalError = () => {
	setLoading(false)
	setModalUsuarioNaoEncontrado(true)
}


// Ao finalizar o processo de Assinatura, redireciona à página de destino.
const redirecionarPagina = async()=>{
	navigate('/rpa/quadroInfo')
}
	

// Irá realizar a operação de validar a captura do rosto no rekognition
const processaCracha = async ( imagemCapturada, codCracha ) =>{
		if((inputRef.current.value).length < 4){
			return;
		}
		// Abrirá modal de loading, para informar o usuário que o reconhecimento está snedo processado.
		setLoading(true)
		try{
			// Busca na base de dados informações referente ao crachá informado.
			const identificaCodUsuario 			= await API.get(`/retornaCodUsuario?codchapa=${inputRef.current.value}`);
			const responseIdentificaCodUsuario 	= identificaCodUsuario.data[0];
			// O crachá informado retornou algum usuário ?
			if(!responseIdentificaCodUsuario) {

				// Não conseguiu identificar o usuário, checar qual erro está retornando.
				const retornarMotError = await API.get(`/erroUserCracha/${codCracha}`);
				const responseMotError = retornarMotError.data;
				
				
				setLoading(false)
				setMsgErrorAss(`${responseMotError}`)
				setModalErrorMv(true)
 
			  // setModalUsuarioNaoEncontrado(true)
			   return
			}
			
			const COD_USUARIO 	= responseIdentificaCodUsuario.CD_USUARIO

			// O crachá trouxe algum usuário? então verifica no S3 se o rosto é correspondente.
			const userCheck 	= await api.post('/rekognition/face',  {'imageSource': imagemCapturada, 'internCode': 1, 'keyName': codCracha, 'option':  'compare', PAPEL_ACAO: 156	});
			
			
			// A imagem é correspondente? então resulta em sucesso.
			if(  (userCheck.data[0].resposta === true) && (userCheck.data[0].user === COD_USUARIO)){
				abreModalSucess();
				await registraHistoricoAutenticacao	( 	location.state.DADOS.COD_AVISO, location.state.DADOS.PROX_ETAPA, codCracha, 'S', `${codCracha}_${COD_USUARIO}_${location.state.DADOS.COD_AVISO}_${location.state.DADOS.PROX_ETAPA}` )
				await uploadPhoto					(	imagemCapturada,codCracha,COD_USUARIO,location.state.DADOS.COD_AVISO,location.state.DADOS.PROX_ETAPA,`${codCracha}_${COD_USUARIO}_${location.state.DADOS.COD_AVISO}_${location.state.DADOS.PROX_ETAPA}`,'S');
				await avancarEtapaCirurgia			(	userCheck.data[0].user)
				return;
			}

			// eslint-disable-next-line default-case
			switch(userCheck.data[0].codigo){
				case 0:
					setLoading		(false)
					setNofaceS3Error(true);
				break;
				case 1:
					abreModalError()
					await registraHistoricoAutenticacao( location.state.DADOS.COD_AVISO, location.state.DADOS.PROX_ETAPA, codCracha, 'N', `${codCracha}_${COD_USUARIO}_${location.state.DADOS.COD_AVISO}_${location.state.DADOS.PROX_ETAPA}` )
					await uploadPhoto(imagemCapturada,codCracha,COD_USUARIO,location.state.DADOS.COD_AVISO,location.state.DADOS.PROX_ETAPA,`${codCracha}_${COD_USUARIO}_${location.state.DADOS.COD_AVISO}_${location.state.DADOS.PROX_ETAPA}`,'N');
		
				break;
			}

		
			

		}catch(error){
			console.log(error)
		}
} 


// Se a operação der certo, este método irá avançar o paciente de etapa.
const avancarEtapaCirurgia = async (usuarioResponsavel) => {
		
	const dataTime 		= new Date
	const moment 		= new Date( dataTime.getTime() )

	const params = {
			avisoCirurgia: 	DadosPaciente.DADOS.COD_AVISO,		
			dtEtapa: 		moment,
			cdEtapa: 		DadosPaciente.INFO_PROX_ETAPA.PROX_ETAPA.PROX_ETAPA,
			usuario: 		usuarioResponsavel,
			salaCirurgia: 	DadosPaciente.DADOS.DS_SAL_CIR,
			tipoAcao: 		'I',
			sublocalidade:  DadosPaciente.CODIGO_SUB_LOCALIDADE
	}
	
	const avancarEtapaCirurgiaReturn  = await API.post('/avancarEtapasCirurgia',  params);
	
	if(!avancarEtapaCirurgiaReturn){
		abreModalError();
	}

	setTimeout(
		await redirecionarPagina()
	, 50000)

};


const closeModal = () => {
	setModalUsuarioNaoEncontrado(false);
};

const session = JSON.parse(localStorage.getItem('CAPP-local'))
const localidadeHeader = `SRPA L${session.SUBLOCALIDADE.split(' ')[1]}`;

return (
		<PageWrapper title={cirurgicalFlow.RPA_ASSINATURA_RECEBIMENTO.text}>
				<Header titulo={localidadeHeader} localidade='Assinatura Colaborador'>
				<Dropdown >
					<DropdownToggle>
						<Button
							color="light"
							icon="menu"
							isLight
						>
							<p> </p>
						</Button>
					</DropdownToggle>
					<DropdownMenu>
						<DropdownItem onClick={() => alterarLocalidade()}>
							Alterar Local
						</DropdownItem>
					</DropdownMenu>
				</Dropdown>
			</Header>
			<Page>

				
				{/** INICIO */}
				<div className={style.container}>
				<header>
					<div>
						<Card className={style.area_header_info_patient} >
								<CardBody>
									<h3>Paciente</h3>
									<h1>{DadosPaciente.DADOS ? DadosPaciente.DADOS.PACIENTE : '- - '}</h1>
								</CardBody>
						</Card>
					</div>
				</header>
				<main className={style.grandArea}>
					{/* área onde haverá a câmera */}
					<div className={style._area_photo_and_chapa}>
						<div className={style.webcam_area}>
							<Webcam ref={webRef} className={style.cam} audio={false}  screenshotFormat="image/jpeg" videoConstraints={videoConstraints} />
						</div>
					</div>
					{/* área onde haverá o input do crachá + teclado */}
					<div className={style._area_teclado}>
						<div style={{"padding": "1rem"}}>
									<div style={{width: "400px"}}>
										<h2>Informe o código do crachá</h2>
										<Input className={style.input_chapa} type="number" disabled  ref={inputRef}/>	
										<br />
									</div>
										<div style={{width: "400px"}}>
											<NumericKeyboard childToParent={childToParent} /> 
										</div>
										{/* área onde haverá o input de assinar + botão de retornar */}
										<div className={style._area_inputs}>
											<div>
												<Button className={style.btn_save} color='success' icon='user' onClick={() => {
														const image = capturarRosto();
														const imageReplaced = image.replace('data:image/jpeg;base64,','');
														processaCracha(imageReplaced,inputRef.current.value);
												}}>
													Assinar
												</Button>
											</div>
											<div>
												<Icon onClick={() => navigate(-1)} icon='reply' color= '#fff' size='5x' forceFamily= 'material' />
											</div>
										</div>
							</div>
					</div>
				</main>
				</div>

				{/** FIM */}

				
			

				<Modal
					id='example-modal'
					isOpen={ModalUsuarioNaoEncontrado}
					titleId='example-title'>
						<ModalHeader className='modal-header-error'>
							<ModalTitle id='example-title'>AVISO</ModalTitle>
						</ModalHeader>
						<ModalBody className='ModalBody'>
							
							<h1>Usuário não encontrado<br /></h1>
							Por favor, tente novamente.
						</ModalBody>
						<ModalFooter>
						
						<ButtonGroup isToolbar="true" size='lg'>
								<Button  color='light' onClick={closeModal}>
									FECHAR
								</Button>								
						</ButtonGroup>
						</ModalFooter>
				</Modal>	
				<Modal
					id='sucess-modal'
					isOpen={modalSuccess}
					titleId='example-titlea'>
						<ModalHeader style={{"backgroundColor":"#37BBAA"}}>
							<ModalTitle id='example-title'>AVISO</ModalTitle>
						</ModalHeader>
						<ModalBody className='ModalBody'>
						<h1>ASSINADO COM SUCESSO!</h1>
						<br /><br />
						</ModalBody>
				</Modal>		
				<Modal
					id='loading'
					isOpen={Loading}
					titleId='loading'>
						<ModalHeader style={{"backgroundColor":"#636363"}}>
							<ModalTitle id='example-title'>ASSINANDO... 
							
							</ModalTitle>
						</ModalHeader>
						<ModalBody className='ModalBody'>
							<br />
							<h1>Assinando...Por favor, aguarde...</h1>
							<br /><br />
						</ModalBody>
				</Modal>	

						
				<Modal isOpen={modalErrorMv}>
						<ModalHeader className='modal-header-error'>
							<ModalTitle id='example-title'>AVISO</ModalTitle>
						</ModalHeader>
						<ModalBody className='ModalBody'>
							<h1>{msgErrorAss}</h1><br />
						</ModalBody>
						<ModalFooter>
						<ButtonGroup isToolbar="true" size='lg'>
								<Button className='btn-style' color='light' onClick={()=>setModalErrorMv(false)}>
									FECHAR
								</Button>								
						</ButtonGroup>
						</ModalFooter>
				</Modal>	

				

				<Modal
					isOpen={nofaceS3Error}>
						<ModalHeader className='modal-header-error'>
							<ModalTitle id='example-title'>AVISO</ModalTitle>
						</ModalHeader>
						<ModalBody className='ModalBody'>
							
							<h1>Face não cadastrada!<br /></h1>
							<h3>Por favor, realize o cadastro e tente novamente.</h3>
						</ModalBody>
						<ModalFooter>
						
						<ButtonGroup isToolbar="true" size='lg'>
								<Button className='btn-style' color='light' onClick={()=>setNofaceS3Error(false)}>
									FECHAR
								</Button>								
						</ButtonGroup>
						</ModalFooter>
				</Modal>
			
			</Page>
		</PageWrapper>
	);
};

export default AssinaturaRecebimento;